body {
  font-family: Arial, sans-serif;
  text-align: center;
  background-color: #f5f5f5;
}

h1 {
  color: #007bff;
}

header {
  background: #222;
  color: white;
  padding: 20px;
}

section {
  margin: 20px;
  padding: 20px;
  background: white;
  border-radius: 8px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
}

a {
  color: #007bff;
  text-decoration: none;
}

footer {
  margin-top: 20px;
  padding: 10px;
  background: #222;
  color: white;
}
